import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem, Modal, ModalHeader, ModalBody, Form, FormGroup, Label, Input, Button } from 'reactstrap';
import { NavLink } from 'react-router-dom';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false,
      isModalOpen: false
    };
  }
  render() {
    return (
      <div>
        <Navbar dark expand="md">
          <NavbarToggler onClick={() => this.toggleNavbar()} />
          <NavbarBrand className="me-auto" href="/"><img src='assets/images/logo.png' height="30" width="40" alt='Ristorante Confusion' /></NavbarBrand>
          <Collapse navbar isOpen={this.state.isNavOpen}>
            <Nav navbar>
              <NavItem>
                <NavLink className="nav-link" to='/home'><span className="fa fa-home fa-lg"></span> Home</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to='/aboutus'><span className="fa fa-info fa-lg"></span> About Us</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to='/menu'><span className="fa fa-list fa-lg"></span> Menu</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className="nav-link" to='/contactus'><span className="fa fa-address-card fa-lg"></span> Contact Us</NavLink>
              </NavItem>
            </Nav>
            <Nav navbar className="ms-auto">
              <NavItem>
                <Button outline onClick={() => this.toggleModal()}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
        <div className="jumbotron">
          <div className="container">
            <div className="row row-header">
              <div className="col-12 col-sm-6">
                <h1>Ristorante Confusion</h1>
                <p>We take inspiration from the Worlds best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
              </div>
            </div>
          </div>
        </div>
        <Modal isOpen={this.state.isModalOpen}>
          <ModalHeader toggle={() => this.toggleModal()}>Login</ModalHeader>
          <ModalBody>
            <Form onSubmit={(event) => this.handleLogin(event)}>
              <FormGroup>
                <Label htmlFor="username">Username</Label>
                <Input type="text" id="username" name="username"
                  innerRef={(input) => this.username = input} />
              </FormGroup>
              <FormGroup>
                <Label htmlFor="password">Password</Label>
                <Input type="password" id="password" name="password"
                  innerRef={(input) => this.password = input} />
              </FormGroup>
              <FormGroup check>
                <Label check>
                  <Input type="checkbox" name="remember"
                    innerRef={(input) => this.remember = input} />Remember me</Label>
              </FormGroup>
              <Button type="submit" value="submit" color="primary">Login</Button>
            </Form>
          </ModalBody>
        </Modal>
      </div>
    );
  }
  toggleNavbar() {
    this.setState({ isNavOpen: !this.state.isNavOpen });
  }
  toggleModal() {
    this.setState({ isModalOpen: !this.state.isModalOpen });
  }
  handleLogin(event) {
    this.toggleModal();
    alert("Username: " + this.username.value + " Password: " + this.password.value + " Remember: " + this.remember.checked);
    event.preventDefault();
  }
}
export default Header;