import React, { Component } from 'react';
import { Routes, Route, Navigate, useParams, useLocation } from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import Menu from './MenuComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import Contact from './ContactComponent';
import Dishdetail from './DishdetailComponent';
import About from './AboutComponent';

// redux
import { connect } from 'react-redux';
import { addComment, fetchDishes, fetchComments, fetchPromos, postComment, fetchLeaders, postFeedback } from '../redux/ActionCreators';
const mapStateToProps = (state) => {
  return {
    dishes: state.dishes,
    comments: state.comments,
    promotions: state.promotions,
    leaders: state.leaders
  }
};
const mapDispatchToProps = (dispatch) => ({
  addComment: (dishId, rating, author, comment) => dispatch(addComment(dishId, rating, author, comment)),
  fetchDishes: () => dispatch(fetchDishes()),
  fetchComments: () => dispatch(fetchComments()),
  fetchPromos: () => dispatch(fetchPromos()),
  postComment: (dishId, rating, author, comment) => dispatch(postComment(dishId, rating, author, comment)),
  fetchLeaders: () => dispatch(fetchLeaders()),
  postFeedback: (newFeedback) => dispatch(postFeedback(newFeedback))
});

class Main extends Component {
  render() {
    const HomePage = () => {
      return (
        <Home
          dish={this.props.dishes.dishes.filter((dish) => dish.featured === true)[0]}
          dishesLoading={this.props.dishes.isLoading}
          dishesErrMess={this.props.dishes.errMess}
          promotion={this.props.promotions.promotions.filter((promo) => promo.featured === true)[0]}
          promoLoading={this.props.promotions.isLoading}
          promoErrMess={this.props.promotions.errMess}
          leader={this.props.leaders.leaders.filter((leader) => leader.featured === true)[0]}
          leaderLoading={this.props.leaders.isLoading}
          leaderErrMess={this.props.leaders.errMess} />
      );
    }
    const MenuPage = () => {
      return (
        <Menu
          dishes={this.props.dishes.dishes}
          isLoading={this.props.dishes.isLoading}
          errMess={this.props.dishes.errMess} />
      );
    }
    const DishdetailPage = () => {
      const params = useParams();
      const dishId = parseInt(params.dishId);
      return (
        <Dishdetail
          comments={this.props.comments.comments.filter((comment) => comment.dishId === dishId)}
          addComment={this.props.addComment}
          postComment={this.props.postComment}
          dish={this.props.dishes.dishes[dishId]}
          isLoading={this.props.dishes.isLoading}
          errMess={this.props.dishes.errMess} />
      );
    }
    const ContactPage = () => {
      return (<Contact postFeedback={this.props.postFeedback} />);
    }
    const AboutPage = () => {
      return (
        <About
          leaders={this.props.leaders.leaders}
          leaderLoading={this.props.leaders.isLoading}
          leaderErrMess={this.props.leaders.errMess} />
      );
    }
    const AnimatedSwitch = () => {
      const location = useLocation();
      return (
        <TransitionGroup>
          <CSSTransition key={location.key} classNames="page" timeout={300}>
            <Routes>
              <Route path="/" element={<Navigate replace to="/home" />} />
              <Route path='/home' element={<HomePage />} />
              <Route path='/menu' element={<MenuPage />} />
              <Route path='/menu/:dishId' element={<DishdetailPage />} />
              <Route path='/contactus' element={<ContactPage />} />
              <Route path='/aboutus' element={<AboutPage />} />
            </Routes>
          </CSSTransition>
        </TransitionGroup>
      );
    };
    return (
      <div>
        <Header />
        <AnimatedSwitch />
        <Footer />
      </div>
    );
  }
  componentDidMount() {
    this.props.fetchDishes();
    this.props.fetchComments();
    this.props.fetchPromos();
    this.props.fetchLeaders();
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Main);