import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalHeader, ModalBody, Row, Label } from 'reactstrap';
import { Link } from 'react-router-dom';
import { format } from 'date-fns';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

import Loading from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';

// react-redux-form-validation
import { Control, LocalForm, Errors } from 'react-redux-form';
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);

class CommentForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false
    };
  }
  render() {
    return (
      <div>
        <Button outline onClick={() => this.toggleModal()}><span className="fa fa-pencil fa-lg"></span> Submit Comment</Button>
        <Modal isOpen={this.state.isModalOpen}>
          <ModalHeader toggle={() => this.toggleModal()}>Submit Comment</ModalHeader>
          <ModalBody>
            <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
              <Row className="form-group ml-1 mr-1">
                <Label htmlFor="rating">Rating</Label>
                <Control.select model=".rating" name="rating" className="form-control">
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </Control.select>
              </Row>
              <Row className="form-group ml-1 mr-1">
                <Label htmlFor="author">Your Name</Label>
                <Control.text model=".author" id="author" name="author" placeholder="Your Name" className="form-control"
                  validators={{ minLength: minLength(3), maxLength: maxLength(15) }} />
                <Errors model=".author" show="touched" className="text-danger"
                  messages={{
                    minLength: 'Must be greater than 2 characters',
                    maxLength: 'Must be 15 characters or less'
                  }} />
              </Row>
              <Row className="form-group ml-1 mr-1">
                <Label htmlFor="comment">Comment</Label>
                <Control.textarea model=".comment" id="comment" name="comment" rows="6" className="form-control" />
              </Row>
              <Row className="form-group ml-1 mr-1">
                <Button type="submit" color="primary">Submit</Button>
              </Row>
            </LocalForm>
          </ModalBody>
        </Modal>
      </div>
    );
  }
  toggleModal() {
    this.setState({ isModalOpen: !this.state.isModalOpen });
  }
  handleSubmit(values) {
    this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    this.toggleModal();
  }
}

class RenderDish extends Component {
  render() {
    const dish = this.props.dish;
    return (
      <FadeTransform in transformProps={{ exitTransform: 'scale(0.5) translateY(-50%)' }}>
        <Card>
          <CardImg top src={baseUrl + dish.image} alt={dish.name} />
          <CardBody>
            <CardTitle tag="h5">{dish.name}</CardTitle>
            <CardText>{dish.description}</CardText>
          </CardBody>
        </Card>
      </FadeTransform>
    );
  }
}

class RenderComments extends Component {
  render() {
    const comments = this.props.comments;
    const dishId = this.props.dishId;
    const postComment = this.props.postComment;
    if (comments != null) {
      const cmts = comments.map((comment) => {
        return (
          <Fade in key={comment.id}>
            <li key={comment.id}>
              <p>{comment.comment}</p>
              <p>-- {comment.author}, {format(new Date(comment.date), 'MMM dd, yyyy')}</p>
            </li>
          </Fade>
        );
      });
      return (
        <div>
          <h4>Comments</h4>
          <ul className="list-unstyled">
            <Stagger in>
              {cmts}
            </Stagger>
          </ul>
          <CommentForm dishId={dishId} postComment={postComment} />
        </div>
      );
    }
    return (<div />);
  }
}

class Dishdetail extends Component {
  render() {
    if (this.props.isLoading) {
      return (
        <div className="container">
          <div className="row">
            <Loading />
          </div>
        </div>
      );
    } else if (this.props.errMess) {
      return (
        <div className="container">
          <div className="row">
            <h4>{this.props.errMess}</h4>
          </div>
        </div>
      );
    } else {
      const dish = this.props.dish;
      const comments = this.props.comments;
      const postComment = this.props.postComment;
      if (dish != null) {
        return (
          <div className="container">
            <div className="row">
              <Breadcrumb>
                <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                <BreadcrumbItem active>{dish.name}</BreadcrumbItem>
              </Breadcrumb>
              <div className="col-12">
                <h3>{dish.name}</h3>
                <hr />
              </div>
            </div>
            <div className="row">
              <div className="col-12 col-md-5 m-1">
                <RenderDish dish={dish} />
              </div>
              <div className="col-12 col-md-5">
                <RenderComments comments={comments} dishId={dish.id} postComment={postComment} />
              </div>
            </div>
          </div>
        );
      }
      return (<div />);
    }
  }
}
export default Dishdetail;